import React from 'react'

function PhonePopup({showpopup, setpopup,phonenum}) {
   return (showpopup)? (
    <div
    className="absolute z-10 mx-[15rem] my-[43rem] rounded-2xl border border-blue-100 bg-white p-8 shadow-lg"
    role="alert"
  >
    <div className="relative items-center sm:flex">
      <span
        class="inline-flex h-8 w-8 flex-shrink-0 items-center justify-center rounded-full bg-indigo-500 text-white"
      >
        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-6 h-6">
  <path stroke-linecap="round" stroke-linejoin="round" d="M20.25 3.75v4.5m0-4.5h-4.5m4.5 0l-6 6m3 12c-8.284 0-15-6.716-15-15V4.5A2.25 2.25 0 014.5 2.25h1.372c.516 0 .966.351 1.091.852l1.106 4.423c.11.44-.054.902-.417 1.173l-1.293.97a1.062 1.062 0 00-.38 1.21 12.035 12.035 0 007.143 7.143c.441.162.928-.004 1.21-.38l.97-1.293a1.125 1.125 0 011.173-.417l4.423 1.106c.5.125.852.575.852 1.091V19.5a2.25 2.25 0 01-2.25 2.25h-2.25z" />
</svg>

      </span>
  
      <p class="mt-3 text-lg font-medium sm:mt-0 sm:ml-3">
       Here is the Phone Number:
      </p>
    </div>
  
    <p class="mt-4 font-bold text-gray-500">
      {phonenum}
    </p>
  
    <div class="mt-6 sm:flex">
      <button
        class="inline-block w-full rounded-lg bg-indigo-500 px-5 py-3 text-center text-sm font-semibold text-white sm:w-auto hover:bg-indigo-800"
        onClick={()=>setpopup(false)}
      >
        Okay
      </button>
  
      
    </div>
  </div>
  ):(<div></div>)
}

export default PhonePopup